.do-scroll-right {
    position: fixed;
    bottom: 40%;
    text-align: center;
    padding: 0 0 0.5em 0;
    min-height: 50px;
    z-index: 999999;
    left: 95%;
    margin-left: -22.5px;
    -ms-transform: rotate(270deg);
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
    -o-transition: bottom 300ms ease-out;
    -webkit-transition: bottom 300ms ease-out;
    transition: bottom 300ms ease-out;
    padding-left: 2px;
}

.do-scroll-right div {
    width: 45px;
    height: 45px;
    border-radius: 45px;
    background-color: #3C4A0B;
    background-color: rgba(108, 116, 82, 0.815);/*#3C4A0B    rgb(91, 99, 64)   */
    background-image: url(../img/icons/dotted-arrow.svg);
    background-repeat: no-repeat;
    background-position: center 70%;
    background-clip: content-box;
    background-size: 64%;
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -o-transition: all 500ms;
    -webkit-transition: all 500ms;
    transition: all 500ms;
    -webkit-touch-callout: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    -khtml-user-select: none;
}

@media (max-width:1199px) {
    .do-scroll-right div{
        width: 40px;
        height: 40px;
        border-radius: 40px;
    }

    .do-scroll-right{
        bottom: 0;
    }
}

/**** ANIMATION ****/

.animated-right {
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
}

.animated-right.bounce-right {
	-webkit-animation-duration: 2s;
	animation-duration: 2s;

	animation-timing-function: ease-out;
	-moz-animation-timing-function: ease-out;
	-webkit-animation-timing-function: ease-out;

	-webkit-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
}

@-webkit-keyframes bounce-right {
	0%, 20%, 80%, 100% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
	}

	60% {
		-webkit-transform: translateY(-30px);
		transform: translateY(-30px);
	}
}

@keyframes bounce-right {
	0%, 20%, 80%, 100% {
		-webkit-transform: translateY(0);
		-ms-transform: translateY(0);
		transform: translateY(0);
	}

	50% {
		-webkit-transform: translateY(-30px);
		-ms-transform: translateY(-30px);
		transform: translateY(-30px);
	}
}

.bounce-right {
	-webkit-animation-name: bounce-right;
	animation-name: bounce-right;
} 

