.wrap {
  width: 600px;
  margin: 0 auto; }

/* -------------------------------------
 * Bar container
 * ------------------------------------- */
.progress-radial {
  float: left;
  margin-right: 20px;
  position: relative;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: 1px solid #756B52;
  background-color: #3C4A0B;
  /*@media (max-width:1199px) {
    margin-right: 0;
    width: 70px;
    height: 70px;
  }*/ }

/* -------------------------------------
 * Optional centered circle w/text
 * ------------------------------------- */
.progress-radial .overlay {
  position: absolute;
  width: 60px;
  height: 60px;
  background-color: #fffde8;
  border-radius: 50%;
  margin-left: 15px;
  margin-top: 15px;
  text-align: center;
  line-height: 60px;
  font-size: 35px;
  color: #5B4832;
  /*@media (max-width:1199px) {
    width: 50px;
    height: 50px;
    margin-left: 12px;
    margin-top: 12px;
    font-size: 30px;
  }*/ }

