#skills__container {
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  vertical-align: middle;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; 
}

.skills__container--content{
  margin-top: 4%;
  margin-bottom: 3%;
}

.box-badge-coder {
  display: inline;
}

.numberB{
  font-size: 0.9em;
  font-weight: bold;
  /*margin-top: 13%;
  margin-left: 15%;*/
  background-color: #3C4A0B;
  color: #ffffff;
  position: relative;
  right: 10%;
  top: 12px;
}
.badge-count {
  width: 25px;
  height: 25px;
  background-color: rgb(121, 113, 95);
  border-radius: 50%;
  position: absolute;
  top: 98%;
  right: 0px;
}

.listSkill{
  display: inline-block;
  margin: 2%;
  width: 130px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.listBadgetsComplete{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.progress {
  position: relative;
  height: 25px;
  margin-bottom: 10px;
}

.progress > .progress-type {
    position: absolute;
    left: 0px;
    font-size: 13px;
    padding: 3px 30px 2px 10px;
    color: #ffffff;
    background-color: rgba(25, 25, 25, 0.2);
}

.progress > .progress-completed {
    position: absolute;
    right: 0px;
    font-weight: 800;
    padding: 3px 10px 2px;
    color: #888;
    font-size: 14px;
}

/*****/
  .section--professional__tech{
    margin-left: 5%;
    margin-top: 4%!important;
  }

  .section-item-title-2{
    margin-bottom: 2%;
  }

  .skill--name{
    /*color: #5B4832;*/
    font-weight: bold;
  }

  @media (max-width:767px) {
    .section-item-title-2{
      font-size: 19px;
      margin-bottom: 4%;
      font-weight: bold;
    }

    .section--professional{
      margin: 0;
      margin-top: 16%;
    }

    .skills__container--content{
      margin-top: 4%;
      margin-bottom: 0;
      padding: 0;
    }
    .listBadgetsComplete{
      padding: 0;
    }
    .section--professional__tech{
      margin: 0;
      margin-left: 7%;
    }

    .skill--name{
      margin-bottom: 1%;
      font-size: 10px;
      margin-left: 0;
    }
  }


.skillTechs{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  /*margin-top: 3.5%;*/
}

@media (min-width: 768px) and (max-width: 1199px) {
  .section-item-title-2{
      font-size: 20px;
    }

    .section--professional{
      margin: 0;
    }

    .skills__container--content{
      margin-top: 3%;
      margin-bottom: 0;
      padding: 0;
    }
    .listBadgetsComplete{
      padding: 0;
      margin-bottom: 0;
    }
}


