.Home{
    background-image: url(../img/background/bg_museumFlip.jpg);
    content: '';
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.lang-change {
    width: auto;
    position: absolute;
    right: 0;
    margin: 0.5em;
    z-index: 50;
}

.left {
    border-bottom-left-radius: 2px;
    border-top-left-radius: 2px;
    border-right: 1px solid rgb(245, 245, 245);
}

.lang-change div {
    display: inline-block;
    padding: 0.5em 0.7em;
    background-color: rgb(245, 245, 245);
    background-color: rgba(245, 245, 245, 0.5);
    float: left;
}

.lang-change a {
    color: white;
    text-decoration: none;
    cursor: pointer;
}

.lang-change .right {
    border-bottom-right-radius: 2px;
    -moz-border-radius-bottomright: 2px;
    border-top-right-radius: 2px;
    -moz-border-radius-topright: 2px;
    cursor: pointer;
}

.lang-change span.active {
    color: #555;
}

/****/

.lang-change-menu {
    width: auto;
    position: absolute;
    right: 0;
    margin: 0.5em;
    z-index: 50;
}

.left-menu {
    border-bottom-left-radius: 2px;
    border-top-left-radius: 2px;
    border: 1px solid #5B4832;
}

.lang-change-menu div {
    display: inline-block;
    padding: 0.5em 0.7em;
    background-color: #5B4832;
    background-color: rgba(245, 245, 245, 0.034);
    float: left;
}

.lang-change-menu a {
    color: #5B4832;
    text-decoration: none;
    cursor: pointer;
}

.lang-change-menu .right-menu {
    border-bottom-right-radius: 2px;
    -moz-border-radius-bottomright: 2px;
    border-top-right-radius: 2px;
    -moz-border-radius-topright: 2px;
    cursor: pointer;
}

.lang-change-menu span.actve-menu {
    color: #555;
}

/*****/

#phrase_mobile{
    padding: 5%;
    padding-top: 7%;
}

#phrase_mobile h4 { 
    font: bold 100% Consolas, Monaco, monospace;
    margin: 2em 1em;
}


