#contact__container {
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  vertical-align: middle;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; 
}

.info__container{
  margin-top: 2%;
}

.info__container--greeting{
  margin-bottom: 5%;
}
.btn-default {
  display: inline-block;
  padding: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  background: #3C4A0B !important;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 0;
  -moz-border-radius: 0;
  -webkit-border-radius: 0;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  border: 0px solid transparent!important;
  position: relative;
  overflow: hidden;
  z-index: 1;
  text-decoration: none;
}

.btn-default:before{
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background: #ffffff;
    z-index: -1;
}

.btn-default:before:hover{
    color: #ffffff;
    border: 0px solid transparent!important;
}


.contact-subtitle-1 {
  color: #5B4832;
  font-size: 14px;
  letter-spacing: -1px;
  font-weight: bold;
  margin-bottom: 8px;
  margin-top: 25px;
  padding-bottom: 5px;
  border-bottom: 1px dotted #5B4832;
}

.contact-subtitle-1 :first-child {
    margin-top: 0;
}

.contact-infos p {
  font-size: 13px;
  line-height: 16px;
}
.form-group {
  margin-bottom: 8px;
}
.form-send {
  width: 50%;
  background-color: #3C4A0B;
}

.form-send:hover {
    background-color: #5B4832 !important;
    color: #ffffff !important;
}

.form-control {
  border-radius: 0;
  -moz-border-radius: 0;
  -webkit-border-radius: 0;
  height: 50px;
}
::-webkit-input-placeholder {
  font-style: italic;
  color: #999999 !important;
}
:-moz-placeholder {
  font-style: italic;
  color: #999999 !important;
}
::-moz-placeholder {
  font-style: italic;
  color: #999999 !important;
}
:-ms-input-placeholder {
  font-style: italic;
  color: #999999 !important;
}

@media (max-width:1199px) {
  .info__container {
    margin-top: 5%;
  }
  .info__container--greeting h3{
    font-size: 20px;
  }
  .info__container--greeting p{
    font-size: 12px;
  }
}


