/*********CONTACT ICONS **********/

.downloadContact{
  padding: 10px;
  border: none;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 700;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  position: relative;
  color: #FFF;
  background-color: #756B52;
  z-index: 20;
  overflow: hidden;
  text-decoration: none;
}

.downloadContentContact{
  margin-top: 4.5%;
}

.contact-social-icons{
  text-align: center;
  margin-top: 7.5%;
}

.contact-social-icons :hover{
  opacity: 0.7;
}

.contact-social-icons a{
  color: #ffffff;
  display: inline-block;
  width: 40px;
  height: 40px;
  text-align: center;
  background: #756B52;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-size: 14px;
  line-height: 40px;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  margin-left: 2%;
  margin-right: 2%;
}

.contact-social-icons a:hover{
  color: #f7f7f7;
  background: #3C4A0B;
}


