#phrase__container{
    padding: 6%;
}

#phrase__container h3 { 
    font: bold 160% Consolas, Monaco, monospace;
    /*border-right: .1em solid black;*/
    width: 16.6em; 
    width: 35.8ch;
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    -webkit-animation: typing 2s steps(27, end),
            blink-caret .5s step-end /*infinite alternate*/;
}

#phrase__container span { 
    font: bold 160% Consolas, Monaco, monospace;
   /* border-right: .1em solid black;*/
    width: 16.5em; 
    width: 35.5ch;
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
    -webkit-animation-delay: 7s;
            animation-delay: 7s;
    -webkit-animation: typing 2s steps(10, end),
            blink-caret .5s step-end /*infinite alternate*/;
}



